  function getBldgFeatStyle(feature) {
    const areaInMeters = feature.get("area_in_meters");
    let color;
    if (areaInMeters > 1300) {
      color = "blue";
    } else {
      color = "yellow";
    }

    const bldgStyle = new Style({
      fill: new Fill({
        // color: "rgba(20,20,20,0.9)"
        color: color
      }),
      stroke: new Stroke({
        color: color,
        width: 1
      })
    });

    return bldgStyle;
  }

  function getCenusTractFeatStyle(feature) {
    const popCount = feature.get("POP_COUNT");
    let color;
    if (popCount > 0 && popCount < 1000) {
      color = "blue";
    } else if (popCount >= 1000 && popCount < 2000) {
      color = "green";
    } else if (popCount >= 2000 && popCount < 3000) {
      color = "yellow";
    } else if (popCount >= 3000 && popCount < 4000) {
      color = "orange";
    } else if (popCount >= 4000) {
      color = "red";
    }

    const censusTractStyle = new Style({
      fill: new Fill({
        color: color
      }),
      stroke: new Stroke({
        color: color,
        width: 1
      })
    });
    return censusTractStyle;
  }

  function addPMTilesWorldBldgFootprintVectorLayer() {
    const URL =
      "https://data.source.coop/vida/google-microsoft-open-buildings/pmtiles/go_ms_building_footprints.pmtiles";

    const lyrSource = new PMTilesVectorSource({
      url: URL,
      attributions: [" Provided by VIDA"]
    });

    const highlightedBldgFootprintFeatStyle = new Style({
      stroke: new Stroke({
        color: "orange",
        width: 1
      }),
      fill: new Fill({
        color: "orange"
      })
    });

    const pmtilesVectorLayer = new VectorTileLayer({
      declutter: true,
      source: lyrSource,
      title: "World Building Footprints",
      style: function (feature) {
        if (
          mHighlightedBldgFootprintFeat &&
          feature.get("area_in_meters") ===
            mHighlightedBldgFootprintFeat.AreaInMeters &&
          feature.get("confidence") === mHighlightedBldgFootprintFeat.Confidence
        ) {
          return highlightedBldgFootprintFeatStyle;
        }
        return getBldgFeatStyle(feature);
      }
    });

    mMapLayers.push(pmtilesVectorLayer);
  }

