  async function createPMTilesMultiLyrVectorLayer() {
    const URL =
      "https://protomaps.github.io/PMTiles/protomaps(vector)ODbL_firenze.pmtiles";

    const lyrSource = new PMTilesVectorSource({
      url: URL
    });

    const fetchSource = new pmtiles.FetchSource(
      URL
      // new Headers(mConfig.Headers)
    );
    const pmtilesSource = new pmtiles.PMTiles(fetchSource);
    const pmtilesHeader = await pmtilesSource.getHeader();
    const pmtilesMetadata = await pmtilesSource.getMetadata();
    // loop through the metadata and create a vector tile source for each
    let vectorLayers;
    if (pmtilesMetadata.json) {
      const j = JSON.parse(pmtilesMetadata.json);

      vectorLayers = j.vector_layers;
    } else {
      vectorLayers = pmtilesMetadata.vector_layers;
    }
    for (let i = 0; i < vectorLayers.length; i += 1) {
      const vectorLyr = vectorLayers[i];
    }

    const pmtilesVectorLayer = new VectorTileLayer({
      declutter: true,
      source: lyrSource,
      style: function (feature) {
        return getBldgFeatStyle(feature);
      }
    });

    mMapLayers.push(pmtilesVectorLayer);
  }

