  // 28Oct2023
  async function callCloudflareWorker(parameter) {
    const workerUrl = `https://geo.kbatchu.workers.dev/`; // Replace  with your actual worker URL
    const response = await fetch(workerUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({ parameter })
    });

    if (!response.ok) {
      throw new Error(
        `Error calling Cloudflare Worker: ${response.statusText}`
      );
    }

    const data = await response.json();
    return data;
  }

