      "</label>" +
      "</span>" +
      "</br><span><label style='font-size:14px;font-weight:bold'>BF Source</label> : <label style='font-weight:500;font-size:14px'>" +
      bfSource +
      "</label></span>" +
      "</br><span><label style='font-size:14px;font-weight:bold'>Boundary ID</label> : <label style='font-weight:500;font-size:14px'>" +
      boundaryID +
      "</label></span>" +
      "</br><span><label style='font-size:14px;font-weight:bold'>Confidence</label> : <label style='font-weight:500;font-size:14px'>" +
      confidence +
      "</label></span>" +
      "</br><span><label style='font-size:14px;font-weight:bold'>Layer Name</label> : <label style='font-weight:500;font-size:14px'>" +
      lyrName +
      "</label></span></div></div>";

    const offset = 30;
    const posX = evt.originalEvent.clientX + offset;
    const posY = evt.originalEvent.clientY + offset / 3;

    d3.select("#" + mMapTooltipElemID).html(tooltipHTML);
    const pos = getToolTipCoords(posX, posY);

    d3.select("#" + mMapTooltipElemID)
